<template>
  <div>
      <p>返却</p>

      <v-container>
        <v-row>
          <v-col cols="6"><v-text-field label="ISBN" outlined v-model="isbn"></v-text-field></v-col>
          <v-col cols="6"><v-btn rounded large color="primary" @click="execReturning">返却</v-btn><v-spacer></v-spacer></v-col>          
        </v-row>
        <v-row>
          <v-col cols="8">

<v-card elevation="2">            
<v-simple-table v-if="new_returning_books.length">
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-left">ISBN</th>
          <th class="text-left">Title </th>
          <th> </th>
        </tr>
      </thead>
      <tbody>
      <tr v-for="(book, index) in new_returning_books" :key="book.isbn">
         <td>{{ book.isbn }}</td>
          <td>{{ book.title }}</td>
          <td><v-btn rounded dark color="green" @click="cancel( index )">cancel</v-btn></td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
</v-card>

          </v-col>
          <v-col cols="4">
          </v-col>
        </v-row>
      </v-container>

      <common-dialog :dialog="dialog" @close-common-dialog="closeCommonDialog" />
  </div>
</template>

<script>
import CommonDialog from '@/global/CommonDialog'
import Util from '@/global/Util.js'

export default {
  name:'Returning',
    props:{
    },
    components: {
      'common-dialog': CommonDialog
    },
    data(){
        return {
            isbn: '',
            book: { isbn:'', title:'', image:'' },
            new_returning_books: [],
            dialog: {on: false, msg: '', type: 'notice', to:'' },
        }
    },
  methods:{
 
    cancel( index ) {
      this.new_returning_books.splice( index, 1 )
    },
    async execReturning() {
      if ( !this.new_returning_books.length ) {
        this.dialog.msg = '返却図書のISBNを入力して下さい。'
        this.dialog.on = true
        return
      }
      let r_books = this.new_returning_books
      //this.$emit('del-borrowing', r_books)
      let result = await Util.deleteBorrowing(r_books)
      console.log(result)
      
      this.new_returning_books = []
      this.dialog.msg = '返却処理を実行しました。'
      this.dialog.on = true
      this.resetForm()
    },
    resetForm() {
      this.isbn = ''
      this.book =  { isbn:'', title:'', image:'' }
      this.new_returning_books =  []
    },
    closeCommonDialog(res) {
      let destination = this.dialog.to
      this.dialog.to = ''
      this.dialog.on = false
      console.log('close common dialog: ' + res)
      if (destination) this.$router.push(destination)
    },

    async isInBarrowing(myisbn) {
      let search_result = await Util.getBorrowedBooks(0, 0, myisbn)
      return (0 < search_result.length)
    },

    async searchBook(isbn) {
      let search_result = await Util.getBooks(0, isbn, '')

      if (search_result.length) {
        
          this.book = search_result[0]
          this.new_returning_books.push( search_result[0] )
          return search_result[0]
      } else {
        return null
      }
    },

  },
  watch: {
  
    'isbn': function( newValue ) {
      if (newValue.length !== 13 ) return
        let myisbn = newValue

        let exist = this.isInBarrowing(myisbn) 
        console.log(myisbn + " exist in borrowing:" + exist)
        if ( !exist ) {
          console.log("check borrowing")
          this.dialog.msg = '登録されていない図書です。'
          this.dialog.on = true
          this.book = ''
          return
        }

this.searchBook( myisbn )
        /*
        let newBook = this.searchBook( myisbn )
        
        console.log("newBook:" + newBook)        
        if ( !newBook ) {
          console.log("check books")
          this.dialog.msg = '登録されていない図書です。'
          this.dialog.on = true
          return
        }

      this.book = newBook
      this.new_returning_books.push( newBook )
      //this.isbn = ''
      */
    },

  },

}
</script>

