<template>
  <div>
    <p>{{  execType }}</p>
    <v-card elevation="2">
      <v-row>
        <v-col cols="4">
            <v-img :src="book.image" width="200"></v-img>
        </v-col>
        <v-col cols="8">
          <v-row>
            <v-col>
            <v-text-field id="isbn" label="ISBN" outlined v-model="isbn" :readonly="editmode" @input="checkISBN"></v-text-field>
            </v-col>
            <v-col>
            <v-radio-group  row v-model="book_api" @change="checkISBN">
              <v-radio label="Google Books" value="google"></v-radio>
            <!-- v-radio disabled label="NDL OpenSearch" value="ndl"></v-radio -->
            </v-radio-group>
            </v-col>
          </v-row>
          
          
          <v-row>
            <v-col cols="8">
          <br>
          <v-text-field label="Title" outlined v-model="book.title"></v-text-field>
          <br>
          <v-text-field label="Authors" outlined v-model="book.authors"></v-text-field>
          <br>
          <p v-show="book_status">
          状態：{{ book_status }}  <br>
          </p>
          <v-btn rounded large color="primary" dark @click="updateBook">{{  execType }}</v-btn>
            </v-col>
          </v-row>

        </v-col>
      </v-row>
    </v-card>
    <common-dialog :dialog="dialog" @close-common-dialog="closeCommonDialog" />
</div>
</template>

<script>
import CommonDialog from '@/global/CommonDialog'
import Util from '@/global/Util.js'

export default {
    name:'BookEdit',
    props:{
    },
    components: {
      'common-dialog': CommonDialog
    },
    data(){
        return {
            book: { id:0, isbn:'', title:'', authors: '', image:'no-image.png' },
            editmode: false,
            book_api: 'google',
            isbn: '',
            execType: '',
            book_status: '',
            dialog: {on: false, msg: 'test text', type: 'notice', to: '' },
        }
    },
  methods:{

    async searchBook(id=0, isbn='', keyword='') {
      let search_result = await Util.getBooks(id, isbn, keyword)
      this.book = search_result[0]
      this.isbn = search_result[0].isbn
    },

    async updateBook() {
        console.log( this.book )
        if ( !this.book.isbn || !this.book.title ) {
          this.dialog.msg = 'ISBNとタイトルを入力して下さい。'
          this.dialog.on = true
          return
        }
      //this.$emit('update-books', this.book )
      let result = await Util.postBook(this.book) 
      console.log('updateBook result: ' + result)
      this.dialog.msg = this.execType + '処理を実行しました。'
      this.dialog.to = '/book_search'
      this.dialog.on = true
      return

    },
    resetForm() {
      this.book =  { isbn:'', title:'', authors: '', image:'no-image.png' }
      this.isbn =  ''      
    },
    closeDialog() {
      this.dialog.on = false
      let destination = this.dialog.to
      this.dialog.to = ''
      if (destination) this.$router.push(destination)
    },
    async checkISBN() {
        if ( this.editmode || this.isbn.length !== 13 ) return
        console.log('checkISBN')
        let search_result =  await Util.getBooks(0, this.isbn, '')
        console.log('checkISBN: ' + search_result)
        if (search_result.length) {
          this.dialog.msg = 'すでに登録済みです。'
          this.dialog.on = true
          this.isbn = ''
          return
        }
        let isbn = this.isbn
 //       console.log(isbn)
 
        if (this.book_api === 'google') {
          this.getBookInfo1(isbn)
        } else if (this.book_api === 'ndl') {
          this.getBookInfo2(isbn)
        }
//        this.$forceUpdate()

    },


    async getBookInfo1(isbn) {
        console.log( "getBookInfo1" )

        this.searchResults = []
        const baseUrl = 'https://www.googleapis.com/books/v1/volumes?'
        const params = {
            q: `isbn:${ isbn }`,
            maxResults:1
        }
        const queryParams = new URLSearchParams(params)
        const res = await fetch(baseUrl + queryParams)
          .then(res=>res.json())
        if (res.totalItems == 0) {
          this.dialog.msg = 'ISBN non found in Google Books'
          this.dialog.on = true
          return
        }
        console.log(res.items)
        const volumeInfo = res.items[0].volumeInfo
        console.log( volumeInfo )
        let title = (volumeInfo.title) ? volumeInfo.title : ''
        let authors = (Array.isArray( volumeInfo.authors ) ) ? volumeInfo.authors.toString() : '' 
        let image = (volumeInfo.imageLinks.thumbnail) ? volumeInfo.imageLinks.thumbnail : 'no-image.png'

        this.book.isbn = isbn
        this.book.title = title
        this.book.authors = authors
        this.book.image = image
    },


  getBookInfo2(isbn) {
    const baseUrl = 'https://iss.ndl.go.jp/api/opensearch?'
    const params = {
        isbn: `isbn:${ isbn }`,
        cnt:1
    }
    const queryParams = new URLSearchParams(params)

    this.axios.get( baseUrl + queryParams, {
      timeout: 5000,
      responseType: 'document'
    })
      .then(function (response) {
          const xmlStr = response.data
          let item_ini = xmlStr.search(/<item>/i)
          
          if (item_ini === -1) {
            this.dialog.msg = 'ISBN non found in NDL'
            this.dialog.on = true
            return
          }

          let item_fin = xmlStr.search(/<\/item>/i) 
          let item  = xmlStr.slice(item_ini+6, item_fin) 

          let title_ini = item.search(/<title>/i)
          let title_fin = item.search(/<\/title>/i) 
          let title  = item.slice(title_ini+7, title_fin) 
          console.log('title : ' + title)

          let author_ini = item.search(/<author>/i)
          let author_fin = item.search(/<\/author>/i) 
          let author  = item.slice(author_ini+8, author_fin) 
          console.log('author : ' + author)

          let image = 'no-image.png'
          console.log(image)

          this.book.isbn = isbn
          this.book.title = title
          this.book.authors = author
          this.book.image = image
      })
      .catch(function (error) {
        console.log(error);
      })
    },


    closeCommonDialog(res) {
      this.dialog.on = false
      console.log('close common dialog: ' + res)
    },

  },

  mounted() {
      const id = this.$route.params.id * 1
      console.log('mounted id: ' + id)
      if ( id == "0" ) {
        this.isbn = ''
        this.editmode = false
        this.execType = '登録'
        this.book_status = ''
      } else {
        console.log('id: ' + id)
        this.editmode = true
        this.searchBook(id)
        console.log('mounted this.book: ' + JSON.stringify(this.book))
        this.execType = '更新'
        /*        
                if ( this.borrowing.some((o)=>o.book_isbn === isbn) ) {
                if (1===1) {
                  this.book_status = '貸出中'
                } else {
                  this.book_status = '在架'
                }
        */
        }
  
  },

}
</script>

