<template>
  <div>
      <p>貸出</p>
      <v-container>
        <v-row>
          <v-col cols="6"><v-text-field label="user id" outlined v-model="user_id" @change="searchUser"></v-text-field></v-col>
          <v-col cols="6">  {{ user.name }}<v-spacer></v-spacer></v-col>
        </v-row>
        <v-row>
          <v-col cols="6"><v-text-field label="ISBN" outlined v-model="isbn" @input="addToCart"></v-text-field></v-col>
          <v-col cols="6"><v-btn rounded large color="primary" @click="execBorrowing">貸出</v-btn><v-spacer></v-spacer></v-col>          
        </v-row>
        <v-row>
          <v-col cols="8">

<v-card elevation="2">
<v-simple-table v-if="new_borrowing_books.length">
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-left">ISBN</th>
          <th class="text-left">Title </th>
          <th class="text-left">Authors </th>
          <th> </th>
        </tr>
      </thead>
      <tbody>
      <tr v-for="(book, index) in new_borrowing_books" :key="book.isbn">
         <td>{{ book.isbn }}</td>
          <td>{{ book.title }}</td>
          <td>{{ book.authors }}</td>
          <td><v-btn rounded dark color="green" @click="cancel( index )">cancel</v-btn></td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
</v-card>

<br><br><br><br>

<v-card elevation="2">
<v-simple-table v-if="borrowed_books.length">
<caption>貸出中の図書</caption>
      <thead>
        <tr>
          <th class="text-left">ISBN</th>
          <th class="text-left">Title </th>
          <th class="text-left">Authors </th>
          <th class="text-left">Date</th>
          <th> </th>
        </tr>
      </thead>
      <tbody>
      <tr v-for="book in borrowed_books" :key="book.id">
         <td>{{ book.isbn }}</td>
          <td>{{ book.title }}</td>
          <td>{{ book.authors }}</td>
          <td>{{ book.create_timestamp }}</td>
        </tr>
      </tbody>
    </v-simple-table>
</v-card>

          </v-col>
          <v-col cols="4">
          </v-col>
        </v-row>
      </v-container>
      
    <common-dialog :dialog="dialog" @close-common-dialog="closeCommonDialog" />     
  </div>
</template>

<script>
import CommonDialog from '@/global/CommonDialog'
import Util from '@/global/Util.js'

export default {
  name:'Borrowing',
    props:{
    },
    components: {
      'common-dialog': CommonDialog
    },    
    data(){
        return {
            user_id: '',
            isbn: '',
            user: { id:0, name:'', email:'' },
            book: { id:0, isbn:'', title:'', authors: '', image:'' },
            new_borrowing_books: [], 
            borrowed_books: [],
            dialog: {on: false, msg: '', type: 'notice', to: '' },
        }
    },
  methods:{

    async searchUser() {
      let id = parseInt(this.user_id)
      if (isNaN(id)) id = 0
      let search_result = await Util.getUsers(id, '')
        
      if ( !search_result.length ) {
        this.dialog.msg = 'この利用者IDは登録されていません。'
        this.dialog.on = true
        return
      }
      
      this.user = search_result[0]
      this.borrowed_books = await Util.getBorrowedBooks(id, 0, '')      
    },

    async execBorrowing() {
      if ( !this.user_id.length || !this.new_borrowing_books.length ) {
      this.dialog.msg = '利用者IDと貸出図書のISBNを入力して下さい。'
      this.dialog.on = true 
      return
      }
      let new_borrowing = []
      const user_id = this.user.id

      this.new_borrowing_books.forEach(function(book){
        new_borrowing.push( {
        user_id: user_id,
        book_id: book.id,
        book_isbn: book.isbn
        } )

      })
      console.log(new_borrowing);
      let res = await Util.postBorrowing(new_borrowing);
      console.log('postBorrowing res: ' + res);
      this.dialog.msg = '貸出処理を実行しました。'
      this.dialog.on = true
      this.resetForm()
    },
    cancel( index ) {
      this.new_borrowing_books.splice( index, 1 )
    },
    resetForm() {
      this.user_id = ''
      this.isbn =  ''
      this.user =  { id:0, name:'', email:'' }
      this.book =  { isbn:'', title:'', image:'' }
      this.new_borrowing_books =  [] 
      this.borrowed_books = []
    },
    
    async addToCart() {
        if ( this.isbn.length !== 13 ) return
        console.log("addToCart ini")
        let myisbn = this.isbn
        let search_result = []
        
        search_result = await Util.getBorrowedBooks(0, 0, myisbn) 
        console.log(myisbn + " exist in borrowing:" + search_result)
        if ( search_result.length ) {
          console.log("check borrowing")
          this.dialog.msg = '貸し出し中の図書です。'
          this.dialog.on = true
          this.book = ''
          return
        }

        search_result = await Util.getBooks(0, myisbn, '')
        console.log("newBook:" + search_result)        
        if ( !search_result.length ) {
          console.log("check books")
          this.dialog.msg = '登録されていない図書です。'
          this.dialog.on = true
          return
        }

        this.book = search_result[0]
        console.log(this.book)
        this.new_borrowing_books.push( this.book )
    },

    closeCommonDialog(res) {
      let destination = this.dialog.to
      this.dialog.to = ''
      this.dialog.on = false
      console.log('close common dialog: ' + res)
      if (destination) this.$router.push(destination)
    },

  },
  watch: {
  },

}
</script>
