<template>
  <div>
    <v-app-bar app color="primary" dark elevation="4">
      <v-toolbar-title> {{ $t("app_name") }}</v-toolbar-title>
      <v-spacer></v-spacer>
      <v-btn text rounded @click="goToHome" :disabled="this.$route.name === 'Home'">{{ $t("btn_home") }}</v-btn>
      <v-btn text rounded @click="goToBorrowing" :disabled="this.$route.name === 'Borrowing'">{{ $t("btn_borrowing") }}</v-btn>
      <v-btn text rounded @click="goToReturning" :disabled="this.$route.name === 'Returning'">{{ $t("btn_returning") }}</v-btn>
      <v-btn text rounded @click="goToBookSearch" :disabled="this.$route.name === 'BookSearch'">{{ $t("btn_booksearch") }}</v-btn>
      <v-btn text rounded @click="goToUserSearch" :disabled="this.$route.name === 'UserSearch'">{{ $t("btn_usersearch") }}</v-btn>
      <v-spacer></v-spacer>
      <!--v-btn color="error" @click="resetDB">{{ $t("btn_reset") }}</v-btn-->
      <img :src="lang_img" width="50" v-on:click="changeLang" />
    </v-app-bar>

    <common-dialog :dialog="dialog" @close-common-dialog="closeCommonDialog" />
  </div>
</template>

<script>
import CommonDialog from '@/global/CommonDialog'

export default {
  name:"Header",
    props:{
      langs: Array
  },
    components: {
      'common-dialog': CommonDialog
    },
  data() {
    return {
      lang_img : '',
      dialog: {on: false, msg: '', type: 'notice', to:'' },
    }
  },
  mounted() {
    this.lang_img = this.$i18n.locale + '.png'
  },
  methods: {
    changeLang () {
      let index = this.langs.findIndex((e) => e === this.$i18n.locale)
      let next_index = (index + 1 ) % this.langs.length
      this.$i18n.locale  = this.langs[ next_index ]
      this.lang_img = this.langs[ next_index ] + '.png'
    },
    goToHome(){
      if (this.$route.name !== 'Home' ) this.$router.push('/')
    },
     goToBorrowing(){
      if (this.$route.name !== 'Borrowing' ) this.$router.push('/borrowing')
    },
    goToReturning(){
      if (this.$route.name !== 'Returning' ) this.$router.push('/returning')
    },
    goToBookSearch(){
      if (this.$route.name !== 'BookSearch' ) this.$router.push('/booksearch')
    },
    goToUserSearch(){
      if (this.$route.name !== 'UserSearch' ) this.$router.push('/usersearch')
    },

    resetDB() {
      this.dialog.msg = 'データをリセットしますか？'
      this.dialog.type = 'confirm'
      this.dialog.to = '/'
      this.dialog.on = true
    },
    closeCommonDialog(res) {
      this.dialog.to = ''
      this.dialog.on = false
      if (res) {
        console.log( 'clicked reset button' )
      }
    },

  }
}
</script>
