<template>
     <v-dialog v-model="dialog.on" max-width="300">
     <v-card>
        <v-card-text>{{ dialog.msg }}</v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <div v-if="dialog.type==='notice'">
            <v-btn color="primary" rounded   @click="closeDialog()">close</v-btn>
          </div>
          <div v-else-if="dialog.type === 'confirm'">
            <v-btn color="primary" rounded   @click="closeDialog(true)">OK</v-btn> 
            <v-btn color="primary" rounded   @click="closeDialog(false)">cancel</v-btn>
          </div>
        </v-card-actions>
      </v-card>
    </v-dialog>
</template>

<script>
export default {
    name:'CommonDialog',
    props:{
        dialog: Object  // {on: Boolean, msg: String, type: String 'notice', 'confirm' } , return Boolean
    },
    data(){
        return {
        }
    },
    methods: {
      closeDialog(res = false) {
        this.$emit('close-common-dialog', res)
      }
    },
}
</script>
