<template>
  <div>
      <p>蔵書検索</p>
         <v-container>
           <v-row>
        <v-col
          cols="7"
          sm="4"
        >
      <v-text-field v-model="keyword"  label="keyword" outlined clearable append-outer-icon="mdi-magnify" @click:append-outer="searchBook"></v-text-field> 
        </v-col>
        <v-col cols="1" sm="1">
        <v-btn class="mx-2" fab dark color="indigo" @click="editBook('0')">
      <v-icon>
        mdi-plus
      </v-icon>
    </v-btn>
        </v-col>
        <v-col cols="4"><v-spacer></v-spacer></v-col>
           </v-row>
 <v-row>
   <v-col cols="8">

<v-card elevation="2">
  <v-simple-table  v-if="search_result.length" >
    <template v-slot:default>
      <thead>
        <tr>
          <th class="text-left">ISBN</th>
          <th class="text-left">Title </th>
          <th class="text-left">Authors </th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="book in search_result" :key="book.id" @click="editBook(book.id)">
          <td>{{ book.isbn }}</td>
          <td>{{ book.title }}</td>
          <td>{{ book.authors }}</td>
        </tr>
      </tbody>
    </template>
  </v-simple-table>
</v-card>

   </v-col>
   <v-col cols="4">
<v-spacer></v-spacer>
  </v-col>
 </v-row>
         </v-container>
  </div>
</template>

<script>
import Util from '@/global/Util.js'

export default {
  name:'BookSearch',
     props:{
    },
    data(){
        return {
            keyword: '',
            search_result: []
        }
    },
  methods:{
    async searchBook() {
      this.search_result = await Util.getBooks(0, '', this.keyword)
    },
    editBook(id) {
      this.$router.push(`/bookedit/${ id }`)
    },
  },
}
</script>
