<template>
  <div>
   
    <p>{{  execType }}</p>
          <v-card elevation="2">

      <v-row>
      
      <v-col cols="8">
 <v-text-field label="ID" outlined v-model="user.id" readonly v-show="execType == '更新'"></v-text-field>
  <br>
  <v-text-field label="Name" outlined v-model="user.name"></v-text-field>
  <br>
    <v-text-field label="E-mail" outlined v-model="user.email"></v-text-field>
  
      </v-col>
      <v-col cols="8">
          <v-btn rounded large color="primary" dark @click="updateUser">{{  execType }}</v-btn>

      </v-col>

      </v-row>
          </v-card>

    <common-dialog :dialog="dialog" @close-common-dialog="closeCommonDialog" />
  </div>
</template>

<script>
import CommonDialog from '@/global/CommonDialog'
import Util from '@/global/Util'

export default {
    name: 'UserEdit',
    props:{
    },
    components: {
      'common-dialog': CommonDialog
    },    
    data(){
        return {
            user: { id:0, name:'', email:'' },
            execType: '',
            dialog: {on: false, msg: '', type: 'notice', to: '' },

        }
    },
  methods:{
    resetForm() {
      this.user = { id:0, name:'', email:'' }      
    },
    async updateUser() {
        //console.log( this.user )

        // メールアドレスの重複チェック
        let search_result = await Util.getUsers(0, this.user.email)
        if ( !this.user.id && search_result.length) {
          this.dialog.msg = 'このメールアドレスは、すでに登録済みです。'
          this.dialog.on = true
          return
        }

        await Util.postUser(this.user)
        this.dialog.msg = this.execType + '処理を実行しました。'
        this.dialog.on = true

    },
    closeCommonDialog(res) {
      let destination = this.dialog.to
      this.dialog.to = ''
      this.dialog.on = false
      console.log('close common dialog: ' + res)
      if (destination) this.$router.push(destination)
    },
    async searchUser(user_id) {
      let search_result = await Util.getUsers(user_id, '')
      this.user = search_result[0]
    },
  },
  mounted() {
      console.log( this.$route.params.id )
      let id = this.$route.params.id * 1
      if ( id !== 0 ) {
        this.execType = '更新'
        this.searchUser(id)

      } else {
        this.execType = '登録'
      }
  },

}
</script>
