const API_USERS = '/users';
const API_BOOKS = '/books';
const API_BORROWING = '/borrowing';

function myFunc() {
          console.log( 'Util myFunc')
}



function getUsers(id=0, keyword='') {
  id = parseInt(id)
  if (isNaN(id)) id = 0

  return new Promise((resolve, reject) => {    
    axios.get(API_USERS, { params: { id: id, keyword: keyword } })
    .then(function (response) {
      //console.log( 'getUser isArray: ' + Array.isArray( response.data ));
      //console.log( response.data );
      //return JSON.parse(response.data)
      resolve(response.data);
    })
    .catch(function (error) {
      console.log(error)
      reject(error);
    })
    .then(function () {
      // 常に実行
    });
  });
}

function postUser(params={id: 0, name: '', email: '' }) {
  return new Promise((resolve, reject) => {    
    axios.post(API_USERS, params )
    .then(function (response) {
      resolve(response.data);
    })
    .catch(function (error) {
      console.log(error)
      reject(error);
    })
    .then(function () {
      // 常に実行
    });
  });
}

function getBooks(id=0, isbn='', keyword='') {
  let params = {id: id, isbn: isbn, keyword: keyword}
  return new Promise((resolve, reject) => {    
    axios.get(API_BOOKS, { params: params } )
    .then(function (response) {
      console.log(response.data)
      resolve(response.data);
    })
    .catch(function (error) {
      console.log(error)
      reject(error);
    })
    .then(function () {
      // 常に実行
    });
  });
}

function postBook(book={id:0,isbn:'',title:'',authors:'',image:''}) {

  return new Promise((resolve, reject) => {    
    //console.log('postBook: ' + JSON.stringify(book))
    axios.post(API_BOOKS,  book )
    .then(function (response) {
      console.log(response);
      resolve( response );
    })
    .catch(function (error) {
      console.log(error);
      reject(error);
    })
    .then(function () {
      // 常に実行
    });
  });
}

function postBorrowing(books=[]) {

  return new Promise((resolve, reject) => {    
    console.log('postBorrowing: ' + JSON.stringify(books))
    axios.post(API_BORROWING,  books )
    .then(function (response) {
      console.log(response);
      resolve( response );
    })
    .catch(function (error) {
      console.log(error);
      reject(error);
    })
    .then(function () {
      // 常に実行
    });
  });
}

function deleteBorrowing(books=[]) {
  return new Promise((resolve, reject) => {    
    //console.log('deleteBorrowing: ' + JSON.stringify(books))
    axios.delete(API_BORROWING,  {data: books })
    .then(function (response) {
      console.log(response);
      resolve( response );
    })
    .catch(function (error) {
      console.log(error);
      reject(error);
    })
    .then(function () {
      // 常に実行
    });
  });
}

function getBorrowedBooks(user_id=0, book_id=0, isbn='') {

  let params={user_id:user_id, book_id:book_id, isbn:isbn };
  console.log('getBorrowedBooks user_id: ' + user_id);
  return new Promise((resolve, reject) => {    
    axios.get(API_BORROWING, { params: params })
    .then(function (response) {
      console.log(response.data);
      resolve( response.data );
    })
    .catch(function (error) {
      console.log(error);
      reject(error);
    })
    .then(function () {
      // 常に実行
    });
  });
}

